
    import { defineComponent } from 'vue'
    export default defineComponent({
        name: "Main",
        data() {
            return {
                pages: [
                    {name: 'Product', router: '/product'},
                    {name: 'Contract', router: '/contract'},
                    {name: 'My', router: '/my'},
                ]
            }
        },
        methods: {
            jump(router: string) {
                this.$router.push(router)
            }
        }
    })
